import Mock from 'mockjs';
import { baseUrl } from '@/utils/global';
import * as login from './modules/login';
import * as user from './modules/user';
import * as dept from './modules/dept';
import * as role from './modules/role';
import * as menu from './modules/menu';
import * as dict from './modules/dict';

// 1. /[], [openMock].
// 2. /[], fnCreate[isOpen].
// 3. /[], [isOpen].
let openMock = true;
// let openMock = false;

fnCreate(login, "login", openMock);
fnCreate(user, "user", openMock);
fnCreate(dept, "dept", openMock);
fnCreate(role, "role", openMock);
fnCreate(menu, "menu", openMock);
fnCreate(dict, "dict", openMock);

/**
 * Mock
 * @param {*} mod 
 * @param {*} modName 
 * @param {*} isOpen Mocktrue - false - 
 */
function fnCreate (mod, modName, isOpen = true) {
  console.log(modName + "'s Mock is " + (isOpen ? "open" : "closed") + ".");
  if (isOpen) {
    for (let key in mod) {
      ((res) => {
        if (res.isOpen !== false) {
          let url = baseUrl;
          if(!url.endsWith("/")) {
            url = url + "/";
          }
          url = url + res.url;
          Mock.mock(new RegExp(url), res.type, (opts) => {
            opts['data'] = opts.body ? JSON.parse(opts.body) : null;
            delete opts.body;
            console.log('\n');
            console.log('%cMock, : ', 'color:blue', opts);
            console.log('%cMock, : ', 'color:blue', res.data);
            return res.data;
          })
        }
      })(mod[key]() || {})
    }
  }
}



